
.step_list ul{
  list-style-type: none;
  width: 90%;
  margin: 0 auto;
  padding: 0 0.25rem;
}

.step_list ul li{
  display: flex;
  align-items: center;
  gap: 1.25rem;
  background-color: var(--clr-blue);
  margin: 1rem auto;
  padding: 1rem;
  border-radius: .75em;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}


.number{
  color: rgb(189, 195, 199);
  margin: 0;
  padding: 0;
  font-size: 3rem;
  font-weight: 800;
}

.text{
  margin: 0;
  padding: 0;
  font-size: 2.25rem;
  font-weight: 800;
  color: #fff;
}

@media (min-width: 420px){
  .step_list ul{
    display: flex;
    flex-direction: row;
    gap: 1.5rem;
  }

  .text{
    font-size: 1.5rem;
  }
}
