@charset "UTF-8";

.hero_text{
  width: 90%;
  margin: 0 auto;
}

.hero_text h1{
  font-size: 3.5rem;
  text-align: center;
  font-weight: 800;
  line-height: 1.5;
}

.hero_text p{
  text-align: center;
  color: rgb(128,128,128, 1);
  font-size: 2.5rem;
}

@media (min-width: 420px){
  .hero_text h1, .hero_text p{
    text-align: start;
    margin: 0;
  }

  .hero_text p{
    font-size: 1.75rem;
  }

}
