#navigation{
  width: 90%;
  margin: 2rem auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.icons{
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
}


.icons a{
  background-color:  rgba(69, 210, 190, 0.4);
  background-color: rgba(236, 236, 236, 0.8);
  padding: 1rem;
  border-radius: 1rem;
}

.svg_color{
  color: #1E75DD;
}

@media (min-width: 420px){
  .icons{
    gap: 3.75rem;
  }
}
